<template>
  <Static>
    <div class="admin">
      <v-card flat>
        <Header />
      </v-card>
      <v-subheader class="bg-color white--text">
        <span class="ml-1">School Administrator</span>
      </v-subheader>
      <v-row justify="center" class="py-4">
        <v-col cols="12" sm="8">
          <v-card flat>
            <v-list nav dense class="" v-for="(set, index) in set" :key="index">
              <v-list-item router :to="set.link">
                <v-list-item-icon>
                  <v-icon color="#610018" size="35">{{ set.icon }}</v-icon>
                </v-list-item-icon>
                <v-list-item-content>
                  <v-list-item-title v-text="set.title" />
                  <v-list-item-subtitle v-text="set.subtitle" />
                </v-list-item-content>
              </v-list-item>
            </v-list>
          </v-card>
        </v-col>
      </v-row>
    </div>
  </Static>
</template>
<script>
import Static from '../../layouts/Static'
import Header from '@/components/Header'
import { au } from '@/firebase/init'
export default {
  name: 'Admin',
  data() {
    return {
      set: [
        {
          icon: 'mdi-android-messages',
          title: 'MESSAGING',
          subtitle: 'Click here to send/view messages',
          link: '/messages',
        },
        {
          icon: 'mdi-folder-download',
          title: 'ASSESSMENTS',
          subtitle: 'Click here to send/view documents',
          link: '/download',
        },
        {
          icon: 'mdi-email-newsletter',
          title: 'NEWSLETTERS',
          link: '/newsletter',
          subtitle: 'Click here to send/view documents',
        },
        {
          icon: 'mdi-trophy-variant-outline',
          title: 'TOP LEARNERS',
          link: '/learners',
          subtitle: 'Click here to capture top learners',
        },
        {
          icon: 'mdi-folder-image',
          title: 'GALLERY',
          subtitle: 'Click here to upload images',
          link: '/gallery',
        },
      ],
      user: null,
    }
  },
  components: { Header, Static },
  created() {
    au.onAuthStateChanged((user) => {
      if (user) {
        this.user = user
      } else {
        this.user = null
        this.$router.push({ name: 'Signin' })
      }
    })
  },
}
</script>